/* Checkbox */
.common__checkboxContainer {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1px;
  margin-right: 1px;
  justify-content: flex-start;
  display: flex;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
}
.common__checkboxContainer input {
  position: absolute;
  opacity: 0;
}
.common__checkboxContainer input:checked + .common__checked {
  display: block;
}
.common__checkbox {
  background: #e8e6de;
  border: 1px solid #cfcdc5;
  border-radius: 3px;
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.common__checked {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: #acaaa2;
  border: 1px solid #9d9b93;
  border-radius: 3px;
  display: none;
}
.common__deleteContainer {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1px;
  margin-right: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
