@media screen and (max-width: 450px) {
  .cartCell {
    margin: 1px;
  }
}
.cartCell__amount-total {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 1.2rem;
  height: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  border: 0.5px solid #c0beb6;
  color: #e62e04;
  font-size: .7rem;
  font-weight: 600;
}
/* Footer */
.footer {
  height: 5rem;
}
/* sticky navigation styles */
.headhesive {
  position: absolute;
  top: -999px;
  opacity: 0;
  transition: 0.3s ease-in-out;
}
.headhesive--stick {
  box-sizing: border-box;
  top: 0;
  width: 100%;
  height: 50px;
  opacity: 1;
  background-color: #ffffff;
  transition: 0.3s ease-in-out;
  z-index: 100;
}
@media screen and (max-width: 1100px) {
  .headhesive--stick {
    position: fixed;
  }
}
.headhesive--unstick {
  position: fixed;
  transform: translateY(-100%);
  width: 100%;
  opacity: 0;
  transition: 0.3s ease-in-out;
}
